<template>
  <div style="display: inline-block;float: left">
    <ImageScreen son :url="url"/>
  </div>
  <div style="display: inline-block;float: left;margin-top: 5%">
    <el-descriptions :title="artwork.name" border="border" direction="horizontal">
      <el-descriptions-item label="作品介绍">{{artwork.introduction}}</el-descriptions-item>
      <el-descriptions-item label="分类">{{artwork.userId}}</el-descriptions-item>
    </el-descriptions>
  </div>

</template>

<script>
import axios from "axios";
import ImageScreen from "@/components/image/ImageTable/ImageScreen";

export default {
  name: "ArtworkInfo",
  components: {ImageScreen},
  data(){
    return{
      artworkId:0,
      imageId:0,
      artwork:{},
      url:"",
      keyword:"",
    }
  },
  created() {
    this.artworkId=this.$route.query.id
    axios.get("http://43.138.12.177:12138/artwork/"+this.artworkId)
        .then((response)=>{
          this.artwork=response.data.data;
          this.imageId=this.artwork.imageId
          let imageId=this.imageId;
          axios.get("http://43.138.12.177:12100/image/"+imageId)
              .then((response)=>{
                this.url=response.data.data.url;
              })
        })

  }
}
</script>

