<template>
  <div class="image-table">
    <el-row gutter="4">
      <el-col :span="8">
        <div>
          <el-image v-for="image in image1" :key="image" :src="image.url" fit="scale-down" @click="jumpToPage(image.artworkId)" :preview-src-list="urlList"/>
        </div>
      </el-col>
      <el-col :span="8">
        <div>
          <el-image v-for="image in image2" :key="image" :src="image.url" fit="scale-down" @click="jumpToPage(image.artworkId)" :preview-src-list="urlList"/>
        </div>
      </el-col>
      <el-col :span="8">
        <div>
          <el-image v-for="image in image3" :key="image" :src="image.url" fit="scale-down" @click="jumpToPage(image.artworkId)" :preview-src-list="urlList"/>
        </div>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import axios from "axios";
export default {
  props:['keyword'],
  data(){
    return {
      image1:[],
      image2:[],
      image3:[],
      urlList:[]
    }
  },
  methods:{
    jumpToPage(artworkId){
      this.$router.push("/artwork?id="+artworkId);
    }
  }
  ,
  created() {
    let keyword=this.keyword;
    axios.get("http://43.138.12.177:12138/artwork?page=1&pageSize=9&word="+keyword)
        .then((response)=>{
          let data=response.data
          let list=data.data.list
          for (let i=0; i<list.length; i++){
            if(i>=0&&i<3){
              axios.get("http://43.138.12.177:12100/image/" +list[i].imageId)
                  .then((response)=>{
                    let d=response.data
                    let url=d.data.url;
                    const image={};
                    image.url=url;
                    image.artworkId=list[i].id;
                    this.image1.push(image)
                    this.urlList.push(url)
                  })
            }else if(i>=3&&i<6){
              axios.get("http://43.138.12.177:12100/image/" +list[i].imageId)
                  .then((response)=>{
                    let d=response.data
                    let url=d.data.url;
                    const image={};
                    image.url=url;
                    image.artworkId=list[i].id;
                    this.image2.push(image)
                    this.urlList.push(url)
                  })
            }else if(i>=6&&i<9){
              axios.get("http://43.138.12.177:12100/image/" +list[i].imageId)
                  .then((response)=>{
                    let d=response.data
                    let url=d.data.url;
                    const image={};
                    image.url=url;
                    image.artworkId=list[i].id;
                    this.image3.push(image)
                    this.urlList.push(url)
                  })
            }
          }
        })
  }
}
</script>

<style scoped>
.image-table{
  background: aliceblue;
}
</style>